import { put, call, delay } from 'redux-saga/effects'
import debugConfig from '~/config/debugConfig'
import { getFavoriteListSuccess, getFavoriteListFailure } from '~/redux/favDux'
import requestManager from '~/sagas/requestManager'

// eslint-disable-next-line import/prefer-default-export
export function* getFavoriteList(api) {
  // only in fixture to get the loading
  if (debugConfig.useFixtures) yield delay(1500)

  const { response, error } = yield call(requestManager, api.getFavoriteList)

  if (response) {
    yield put(getFavoriteListSuccess(response.data))
  }

  if (error) yield put(getFavoriteListFailure(error))
}
