import { put, call, delay } from 'redux-saga/effects'
import Router from 'next/router'
import {
  validateUsernameSuccess,
  validateSecureWordSuccess,
  getAuthSuccess,
  getAuthFailure,
  logoutAuthSuccess,
  logoutAuthFailure,
} from '~/redux/userDux'
import { toggleLoading } from '~/redux/uiDux'
import requestManager from '~/sagas/requestManager'

export function* validateUsername() {
  // set loading
  yield put(toggleLoading(true))

  // play some delay for fake loading.
  yield delay(1000)

  // complete the call and toggle loading
  yield put(validateUsernameSuccess())
  yield put(toggleLoading(false))
}

export function* validateSecureWord() {
  // set loading
  yield put(toggleLoading(true))

  // play some delay for fake loading.
  yield delay(300)

  // complete the call and toggle loading
  yield put(validateSecureWordSuccess())
  yield put(toggleLoading(false))
}

export function* getAuth(api, { payload }) {
  // set loading
  yield put(toggleLoading(true))

  const { username } = payload

  try {
    const { response, error } = yield call(
      requestManager,
      api.getAuth,
      username
    )

    if (response) {
      // we don't actually have a token from our API, so we use the github user ID instead
      const { id } = response.data

      // save in localstorage
      localStorage.setItem('accessToken', JSON.stringify(id))

      yield put(getAuthSuccess(JSON.stringify(id)))

      Router.replace('/dashboard')
    }

    if (error) throw new Error(error)
  } catch (error) {
    yield put(getAuthFailure(error))
  } finally {
    yield put(toggleLoading(false))
  }
}

export function* getLogout(api) {
  const { response, error } = yield call(requestManager, api.getLogout)

  if (response) {
    yield put(logoutAuthSuccess())

    // clear storage
    localStorage.clear()
    localStorage.setItem('logout', Date.now())

    window.location = '/login'
  }

  if (error) yield put(logoutAuthFailure(error))
}
