import apisauce from 'apisauce'
import fixtures from './fixtureApi'

const create = () => {
  const api = apisauce.create({
    baseURL: process.env.BASE_URL,
    headers: {
      Accept: 'application/vnd.github.v3+json',
    },
  })

  // example login API. since endpoint doesn't exists, it will only return mock object with mock token from the fixtures API

  /**
   * this the actual look of the most likely possible auth API
   * in this case we do passwordless login
   * @param {string} userName
   */
  const getAuth = ({ userName }) => api.get(`/users/${userName}`)

  // this is for the fixtures
  // const getAuth = () => fixtures.getAuth();
  const getLogout = () => fixtures.getLogout()

  const getRepoDetails = repo => api.get(`/repos/${repo}`)
  const getLatestCommit = repo => api.get(`/repos/${repo}/commits`)
  const getContributors = repo => api.get(`/repos/${repo}/contributors`)
  const getLanguages = repo => api.get(`/repos/${repo}/languages`)

  return {
    getAuth,
    getLogout,
    getRepoDetails,
    getLatestCommit,
    getContributors,
    getLanguages,
  }
}

export default {
  create,
}
