import autodux, { id, assign } from 'autodux'

const initialState = {
  loading: false,
  isRouteChanging: false,
  isSamePath: false,
}

export const {
  reducer,
  initial,
  slice,
  actions: { toggleLoading, setRouteChanging, setRouteCompleteChanging },
  selectors,
} = autodux({
  slice: 'ui',
  initial: initialState,
  actions: {
    toggleLoading: (state, payload) => ({ ...state, loading: payload }),
    setRouteChanging: (state, { isRouteChanging, isSamePath }) => ({
      ...state,
      isRouteChanging,
      isSamePath,
    }),
    setRouteCompleteChanging: assign('isRouteChanging'),
  },
  selectors: {
    getValue: id,
  },
})
