import { put, call, delay } from 'redux-saga/effects'
import debugConfig from '~/config/debugConfig'
import {
  getAllTransactionsSuccess,
  getAllTransactionsFailure,
  getTransactionSuccess,
  getTransactionFailure,
} from '~/redux/transactionsDux'
import { toggleLoading } from '~/redux/uiDux'
import requestManager from '~/sagas/requestManager'

export function* getAllTransactions(api, { payload }) {
  yield put(toggleLoading(true))
  const { accountNo, type } = payload

  // only in fixture to get the loading
  if (debugConfig.useFixtures) yield delay(1500)

  try {
    const { response, error } = yield call(
      requestManager,
      api.getAllTransactions,
      accountNo,
      type
    )

    if (response.ok) {
      yield put(getAllTransactionsSuccess(response.data))
    }

    if (error) throw new Error(error)
  } catch (error) {
    yield put(getAllTransactionsFailure(error))
  } finally {
    yield put(toggleLoading(false))
  }
}

export function* getTransaction(api, { payload }) {
  const { accountNo, id } = payload

  // only in fixture to get the loading
  if (debugConfig.useFixtures) yield delay(3000)

  try {
    const { response, error } = yield call(
      requestManager,
      api.getTransaction,
      accountNo,
      id
    )
    if (error) throw new Error(error)
    else if (response.ok) {
      yield put(getTransactionSuccess(response.data))
    }
  } catch (error) {
    yield put(getTransactionFailure(error.message))
  }
}
