/* eslint-disable global-require */
import { combineReducers } from 'redux'
import configureStore from '~/redux/createStore'
import { reducer as user } from '~/redux/userDux'
import { reducer as accounts } from '~/redux/accountsDux'
import { reducer as transactions } from '~/redux/transactionsDux'
import { reducer as statements } from '~/redux/statementsDux'
import { reducer as fav } from '~/redux/favDux'
import { reducer as ui } from '~/redux/uiDux'
import rootSaga from '~/sagas'

export const rootReducer = combineReducers({
  user,
  accounts,
  transactions,
  statements,
  fav,
  ui,
})

export default initialState => {
  const configStore = configureStore(rootReducer, rootSaga, initialState)
  const { store, sagaMiddleware } = configStore

  if (module.hot) {
    module.hot.accept(() => {
      // only in DEV for hot reload
      // eslint-disable-next-line import/no-self-import
      const nextRootReducer = require('./').rootReducer
      store.replaceReducer(nextRootReducer)

      const newYieldedSagas = require('../sagas').default
      store.sagaTask.cancel()
      store.sagaTask.done.then(() => {
        store.runSagaTask = () => {
          store.sagaTask = sagaMiddleware.run(newYieldedSagas)
        }

        store.runSagaTask()
      })
    })
  }

  return store
}
