import autodux, { id, assign } from 'autodux'

const initialState = {
  statements: [],
  statement: null,
}

export const {
  reducer,
  initial,
  slice,
  actions: {
    getAllStatements,
    getAllStatementsSuccess,
    getAllStatementsFailure,
    getStatement,
    getStatementSuccess,
    getStatementFailure,
  },
  selectors,
} = autodux({
  slice: 'statements',
  initial: initialState,
  actions: {
    getAllStatements: state => ({
      ...state,
      statements: [],
    }),
    getAllStatementsSuccess: assign('statements'),
    getAllStatementsFailure: state => state,
    getStatement: state => ({
      ...state,
      statement: null,
    }),
    getStatementSuccess: assign('statement'),
    getStatementFailure: state => state,
  },
  selectors: {
    getValue: id,
  },
})
