import autodux, { id, assign } from 'autodux'

const initialState = {
  transactions: [],
  transaction: null,
  searchQuery: '',
  type: 'all',
  dateFilter: null,
  loading: false,
}

export const {
  reducer,
  initial,
  slice,
  actions: {
    getAllTransactions,
    getAllTransactionsSuccess,
    getAllTransactionsFailure,
    getTransaction,
    getTransactionSuccess,
    getTransactionFailure,
  },
  selectors,
} = autodux({
  slice: 'transactions',
  initial: initialState,
  actions: {
    getAllTransactions: (state, { type }) => ({
      ...state,
      transactions: [],
      type,
    }),
    getAllTransactionsSuccess: assign('transactions'),
    getAllTransactionsFailure: state => state,
    getTransaction: state => ({
      ...state,
      transaction: null,
      loading: true,
    }),
    getTransactionSuccess: (state, payload) => ({
      ...state,
      transaction: payload,
      loading: false,
    }),
    getTransactionFailure: state => ({ ...state, loading: false }),
  },
  selectors: {
    getValue: id,
  },
})
