import autodux, { id, assign } from 'autodux'

const initialState = {
  accountsList: [],
  specificAccountsList: [],
  selectedAccountDetails: null,
}

export const {
  reducer,
  initial,
  slice,
  actions: {
    getAllAccounts,
    getAllAccountsSuccess,
    getAllAccountsFailure,
    getSavingsAccountsAndRedirect,
    getSpecificAccountsList,
    getSpecificAccountsListSuccess,
    getSpecificAccountsListFailure,
    getSelectedAccount,
    getSelectedAccountSuccess,
    getSelectedAccountFailure,
  },
  selectors,
} = autodux({
  slice: 'accounts',
  initial: initialState,
  actions: {
    getAllAccounts: state => state,
    getAllAccountsSuccess: assign('accountsList'),
    getAllAccountsFailure: state => state,
    getSavingsAccountsAndRedirect: state => state,
    getSpecificAccountsList: state => state,
    getSpecificAccountsListSuccess: assign('specificAccountsList'),
    getSpecificAccountsListFailure: state => state,
    getSelectedAccount: state => state,
    getSelectedAccountSuccess: assign('selectedAccountDetails'),
    getSelectedAccountFailure: state => state,
  },
  selectors: {
    getValue: id,
  },
})
