import { put, call, delay } from 'redux-saga/effects'
import debugConfig from '~/config/debugConfig'
import {
  getAllStatementsSuccess,
  getAllStatementsFailure,
  getStatementSuccess,
  getStatementFailure,
} from '~/redux/statementsDux'
import { toggleLoading } from '~/redux/uiDux'
import requestManager from '~/sagas/requestManager'

export function* getAllStatements(api, { payload }) {
  yield put(toggleLoading(true))

  // only in fixture to get the loading
  if (debugConfig.useFixtures) yield delay(1500)

  try {
    const { response, error } = yield call(
      requestManager,
      api.getAllStatements,
      payload
    )

    if (response.ok) {
      yield put(getAllStatementsSuccess(response.data))
    }

    if (error) throw new Error(error)
  } catch (error) {
    yield put(getAllStatementsFailure(error))
  } finally {
    yield put(toggleLoading(false))
  }
}

export function* getStatement(api, { payload }) {
  yield put(toggleLoading(true))
  const { accountNo, id } = payload

  // only in fixture to get the loading
  if (debugConfig.useFixtures) yield delay(3000)

  try {
    const { response, error } = yield call(
      requestManager,
      api.getStatement,
      accountNo,
      id
    )
    if (error) throw new Error(error)
    else if (response.ok) {
      yield put(getStatementSuccess(response.data))
    }
  } catch (error) {
    yield put(getStatementFailure(error))
  } finally {
    yield put(toggleLoading(false))
  }
}
