import { put, call, delay } from 'redux-saga/effects'
import Router from 'next/router'
import debugConfig from '~/config/debugConfig'
import {
  getAllAccountsSuccess,
  getAllAccountsFailure,
  getSpecificAccountsListSuccess,
  getSpecificAccountsListFailure,
  getSelectedAccountSuccess,
  getSelectedAccountFailure,
} from '~/redux/accountsDux'
import { toggleLoading } from '~/redux/uiDux'
import requestManager from '~/sagas/requestManager'
import { objectToMergedArray } from '~/utilities/transforms'

export function* getAllAccounts(api) {
  yield put(toggleLoading(true))

  // only in fixture to get the loading
  if (debugConfig.useFixtures) yield delay(1500)

  try {
    const { response, error } = yield call(requestManager, api.getAllAccounts)

    if (response) {
      const accounts = objectToMergedArray(response.data)

      yield put(getAllAccountsSuccess(accounts))
    }

    if (error) throw new Error(error)
  } catch (error) {
    yield put(getAllAccountsFailure(error))
  } finally {
    yield put(toggleLoading(false))
  }
}

/**
 * Get all of the savings accounts for the particular user id and redirect
 * to the first in the list
 * @param {Object} api the API object
 * @param {Object} payload the user id
 */
export function* getSavingsAccountsAndRedirect(api, { payload }) {
  yield put(toggleLoading(true))

  try {
    const { response, error } = yield call(
      requestManager,
      api.getSavingsAccounts,
      payload
    )

    if (response) {
      if (response.data && response.data.length) {
        yield put(getSpecificAccountsListSuccess(response.data))

        // redirect to the first account
        const firstAccount = response.data[0]

        Router.replace(
          '/accounts/savings/[accountNo]',
          `/accounts/savings/${firstAccount.accountNo}`
        )
      } else {
        // it got no savings account, which shouldn't happen. Lets redirect them somewhere,
        // maybe, the all accounts list.
        Router.replace(`/accounts`)
      }
    }

    if (error) throw new Error(error)
  } catch (error) {
    yield put(getSpecificAccountsListFailure(error))
  } finally {
    yield put(toggleLoading(false))
  }
}

