/**
 * Get the savings account details for the particular account no
 * @param {Object} api the API object
 * @param {Object} payload the account no
 */
export function* getSavingsAccount(api, { payload }) {
  yield put(toggleLoading(true))

  // we add small animation to the account details,
  // so the delay give that outro animation
  yield delay(1000)

  try {
    const { response, error } = yield call(
      requestManager,
      api.getSavingsAccount,
      payload
    )

    if (response) yield put(getSelectedAccountSuccess(response.data))

    if (error) throw new Error(error)
  } catch (error) {
    yield put(getSelectedAccountFailure(error))
  } finally {
    yield put(toggleLoading(false))
  }
}
