/**
 * obj is a object with keys and have a value of array
 * eg: {
 *  peoples: [...]
 * }
 * The function will return an array of object with the array of objects.
 * The key of the object will be merged into the output with key "key" while its value will be under
 * "values" key.
 * eg: [{
 *  key: 'peoples',
 *  values: [...]
 * }]
 * @param {Object} obj
 * return [Object]
 */
// eslint-disable-next-line import/prefer-default-export
export const objectToMergedArray = obj => {
  const keys = Object.keys(obj)
  const values = Object.values(obj)

  // merged together as an array
  const merged = keys.map((key, index) => {
    return {
      key,
      values: values[index],
    }
  })

  return merged
}
