import React from 'react'
import App from 'next/app'
import { Provider } from 'react-redux'
import withRedux from 'next-redux-wrapper'
import withReduxSaga from 'next-redux-saga'
import { ThemeProvider, DomusTheme } from '@domus/domus-ui'
import createStore from '~/redux'
import '~/config'

const Noop = ({ children }) => children

class AppRoot extends App {
  render() {
    const { Component, pageProps, store } = this.props
    const Layout = Component.Layout || Noop
    const footerDockedConfig = Component.dockedFooter || null

    return (
      <Provider store={store}>
        <ThemeProvider theme={DomusTheme}>
          <Layout footerDockedConfig={footerDockedConfig}>
            <Component {...pageProps} />
          </Layout>
        </ThemeProvider>
      </Provider>
    )
  }
}

export default withRedux(createStore)(withReduxSaga(AppRoot))
