import autodux, { id } from 'autodux'

const initialState = {
  token: '',
  profile: null,
  isUsernameValidated: false,
  isSecureWordValidated: false,
  secureWordBlob:
    '',
}

export const {
  reducer,
  initial,
  slice,
  actions: {
    validateUsername,
    validateUsernameSuccess,
    validateUsernameFailure,
    validateSecureWord,
    validateSecureWordSuccess,
    validateSecureWordFailure,
    getAuth,
    getAuthSuccess,
    getAuthFailure,
    logoutAuth,
    logoutAuthSuccess,
    logoutAuthFailure,
  },
  selectors,
} = autodux({
  slice: 'user',
  initial: initialState,
  actions: {
    validateUsername: state => state,
    validateUsernameSuccess: state => ({ ...state, isUsernameValidated: true }),
    validateUsernameFailure: state => state,
    validateSecureWord: state => state,
    validateSecureWordSuccess: state => ({
      ...state,
      isSecureWordValidated: true,
    }),
    validateSecureWordFailure: state => state,
    getAuth: state => state,
    getAuthSuccess: (state, payload) => ({ ...state, token: payload }),
    getAuthFailure: state => state,
    getProfile: state => state,
    getProfileSuccess: (state, payload) => ({
      ...state,
      profile: payload,
    }),
    logoutAuth: () => initialState,
    logoutAuthSuccess: () => initialState,
    logoutAuthFailure: () => initialState,
  },
  selectors: {
    getValue: id,
  },
})
