import { tokenCreds, favList, accounts, savingsAccounts } from '../fixtures'

export default {
  // Functions return fixtures
  getAuth: () => ({
    ok: true,
    data: tokenCreds,
  }),

  getLogout: () => ({
    ok: true,
    data: {},
  }),

  getFavoriteList: () => ({
    ok: true,
    data: favList,
  }),

  getAllAccounts: () => ({
    ok: true,
    data: accounts,
  }),

  getSavingsAccounts: () => ({
    ok: true,
    data: savingsAccounts,
  }),

  getSavingsAccount: accountNo => {
    const account = savingsAccounts.find(acc => acc.accountNo === accountNo)
    const withRecentTransactions =
      account.mostRecentTransactions.length > 10
        ? account.mostRecentTransactions.splice(0, 9)
        : account.mostRecentTransactions
    const data = {
      ...account,
      mostRecentTransactions: withRecentTransactions,
    }

    return {
      ok: true,
      data,
    }
  },

  getAllTransactions: (accountNo, type) => {
    const account = savingsAccounts.find(acc => acc.accountNo === accountNo)
    const { mostRecentTransactions } = account
    const transactionsWithType = mostRecentTransactions.filter(
      transaction => transaction.type === type
    )
    const transactions =
      type === 'all' ? mostRecentTransactions : transactionsWithType

    return {
      ok: true,
      data: transactions || [],
    }
  },

  getTransaction: (accountNo, id) => {
    const account = savingsAccounts.find(acc => acc.accountNo === accountNo)
    const { mostRecentTransactions } = account

    // we do not do strict comparison because we know we're sending a string
    // down here, while the mock id is in number
    // This should be consistent once we know the actual data type of the id
    // eslint-disable-next-line eqeqeq
    const transaction = mostRecentTransactions.find(t => t.id == id)

    if (transaction) {
      return {
        ok: true,
        data: transaction,
      }
    }

    return {
      ok: false,
    }
  },

  getAllStatements: accountNo => {
    const account = savingsAccounts.find(acc => acc.accountNo === accountNo)
    const { statements } = account
    return {
      ok: true,
      data: statements || [],
    }
  },

  getStatement: (accountNo, id) => {
    const account = savingsAccounts.find(acc => acc.accountNo === accountNo)
    const { statements } = account
    const mergedStatements = statements.reduce((p, s) => [...p, ...s.data], [])
    // eslint-disable-next-line eqeqeq
    const statement = mergedStatements.find(s => s.id == id)

    return {
      ok: true,
      data: statement,
    }
  },
}
