import { takeLatest, all } from 'redux-saga/effects'
import API from '~/services/api'
import fixtureApi from '~/services/fixtureApi'
import debugConfig from '~/config/debugConfig'

/* ------------- Types ------------- */
import {
  validateUsername as validateUsernameType,
  validateSecureWord as validateSecureWordType,
  getAuth as authType,
  logoutAuth as logoutType,
} from '~/redux/userDux'
import {
  getAllAccounts as getAllAccountsType,
  getSavingsAccountsAndRedirect as getSavingsAccountsAndRedirectType,
  getSpecificAccountsList as getSpecificAccountsListType,
  getSelectedAccount as getSelectedAccountType,
} from '~/redux/accountsDux'
import {
  getAllTransactions as getAllTransactionsType,
  getTransaction as getTransactionType,
} from '~/redux/transactionsDux'
import {
  getAllStatements as getAllStatementsType,
  getStatement as getStatementType,
} from '~/redux/statementsDux'
import { getFavoriteList as getFavoriteListType } from '~/redux/favDux'

/* ------------- Sagas ------------- */

import {
  validateUsername,
  validateSecureWord,
  getAuth,
  getLogout,
} from '~/sagas/userSaga'
import {
  getAllAccounts,
  getSavingsAccountsAndRedirect,
  getSavingsAccounts,
  getSavingsAccount,
} from '~/sagas/accountsSaga'
import { getAllTransactions, getTransaction } from '~/sagas/transactionsSaga'

import { getAllStatements, getStatement } from '~/sagas/statementsSaga'

import { getFavoriteList } from '~/sagas/favSaga'

/* ------------- API ------------- */
const api = debugConfig.useFixtures ? fixtureApi : API.create()

/* ------------- Connect Types To Sagas ------------- */

export default function* root() {
  yield all([
    takeLatest(validateUsernameType.type, validateUsername, api),
    takeLatest(validateSecureWordType.type, validateSecureWord, api),
    takeLatest(getFavoriteListType.type, getFavoriteList, api),
    takeLatest(getAllAccountsType.type, getAllAccounts, api),
    takeLatest(
      getSavingsAccountsAndRedirectType.type,
      getSavingsAccountsAndRedirect,
      api
    ),
    takeLatest(getSpecificAccountsListType.type, getSavingsAccounts, api),
    takeLatest(getSelectedAccountType.type, getSavingsAccount, api),
    takeLatest(getAllTransactionsType.type, getAllTransactions, api),
    takeLatest(getTransactionType.type, getTransaction, api),
    takeLatest(getAllStatementsType.type, getAllStatements, api),
    takeLatest(getStatementType.type, getStatement, api),
    takeLatest(authType.type, getAuth, api),
    takeLatest(logoutType.type, getLogout, api),
  ])
}
