import autodux, { id } from 'autodux'

const initialState = {
  loading: false,
  favoriteList: [],
}

export const {
  reducer,
  initial,
  slice,
  actions: { getFavoriteList, getFavoriteListSuccess, getFavoriteListFailure },
  selectors,
} = autodux({
  slice: 'ui',
  initial: initialState,
  actions: {
    getFavoriteList: state => ({ ...state, loading: true }),
    getFavoriteListSuccess: (state, payload) => ({
      ...state,
      favoriteList: payload,
      loading: false,
    }),
    getFavoriteListFailure: state => ({ ...state, loading: false }),
  },
  selectors: {
    getValue: id,
  },
})
